import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App'
import * as firebase from 'firebase'
import router from './router'
import { store } from './store'
import DateFilter from './filters/date'
import AlertCmp from './components/Shared/Alert.vue'
import EditProduct from './components/Meetup/Edit/EditProducts.vue'
import EditProductDate from './components/Meetup/Edit/EditProductDateDialog.vue'
import EditProductTime from './components/Meetup/Edit/EditProductTimeDialog.vue'
import RegisterDialog from './components/Meetup/Registration/RegisterDialog.vue'
Vue.use(Vuetify)
Vue.config.productionTip = false

Vue.filter('date', DateFilter)
Vue.component('app-alert', AlertCmp)
Vue.component('app-edit-product-details-dialog', EditProduct)
Vue.component('app-edit-product-date-dialog', EditProductDate)
Vue.component('app-edit-product-time-dialog', EditProductTime)
Vue.component('app-register-dialog', RegisterDialog)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyBzL5_BExsqPVCb5TgnPhLvCMWopMgWJS0',
      authDomain: 'tamiasl-c1764.firebaseapp.com',
      databaseURL: 'https://tamiasl-c1764.firebaseio.com',
      projectId: 'tamiasl-c1764',
      storageBucket: 'gs://tamiasl-c1764.appspot.com'
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
        this.$store.dispatch('fetchUserData')
      }
    })
    this.$store.dispatch('loadedMeetups')
  }
})



