<template lang="html">
    <v-container>
      <v-layout row wrap>
        <v-flex xs12 sm6 class="text-xs-center text-sm-right mt-4">
          <v-btn large router to="/meetups" class="warning">Explore Meetups</v-btn>
        </v-flex>
        <v-flex xs12 sm6 class="text-xs-center text-sm-left mt-4">
          <v-btn large router to="/meetup/new" class="warning">Organize Meetup</v-btn>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex xs12 class="text-xs-center">
          <v-progress-circular
          indeterminate
          class="red--text"
          v-if="loading"></v-progress-circular>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="mt-2" v-if="!loading">
        <v-flex xs12 class="mr-2">
          <v-carousel style="cursor: pointer;" class="img">
            <v-carousel-item
            v-for="meetup in meetups"
            :src="meetup.imageUrl"
            :key="meetup.id"
            @click="onLoadMeetup(meetup.id)">
            <div class="title">
              {{ meetup.title }}
            </div>
          </v-carousel-item>
          </v-carousel>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="mt-2">
        <v-flex xs12 class="text-xs-center">
          <p>Join our awesome Meetup</p>
        </v-flex>
      </v-layout>

      <v-layout row wrap class="mt-2">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular
        indeterminate
        color="red"
        v-if="loading"></v-progress-circular>
      </v-flex>
     <v-flex xs12 sm6 md4
     v-for="card in cards"
       :key="card.id">
     <div >
       <v-card v-if="!loading">
       <v-card-media
       :src="card.imageUrl"
       height="200px"
       @click="onLoadItem(card.id)"
       style="cursor:pointer;">
       </v-card-media>
       <v-card-title primary-title>
         <div>
         <h3  class="headline mb-0">{{ card.title}}</h3>
         <div>{{ card.description }}</div>
         </div>
       </v-card-title>
       <v-card-actions>
        <h6> {{ card.location }}</h6>
         <v-spacer></v-spacer>
         <router-link class="btn-link"
         :to="{name: 'Meetup', params: {id: card.id}}">
         <v-btn flat color="orange">
            View Meetup
         </v-btn>
         </router-link>
       </v-card-actions>
       </v-card>
     </div>
     </v-flex>
   </v-layout>

    </v-container>
</template>

<script>
export default {
  computed: {
    meetups () {
      return this.$store.getters.featuredMeetups
    },
    cards () {
      return this.$store.getters.loadedMeetups
    },
    loading () {
      return this.$store.getters.loading
    }
  },
  methods: {
    onLoadMeetup (id) {
      this.$router.push('/meetups/' + id)
    }
  }
}
</script>

<style lang="css" scoped>
  .img {
    height: 450px;
    max-height: 450px;
   }
  .title {
    position: absolute;
    bottom: 50px;
    background-color: rgba(0,0,0,0.5);
    color: #fff;
    font-size: 2em;
    padding: 20px;
  }
  .btn-link {
    text-decoration: none;
  }
</style>



