<template lang="html">
  <v-container>
    <v-layout row wrap v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular
        indeterminate
        class="red--text"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-else>
      <v-flex xs12>
        <v-card>
          <v-card-title>
            <h5 class="primary--text">{{ meetup.title }}</h5>
            <template v-if="userIsCreator">
              <v-spacer></v-spacer>
              <app-edit-product-details-dialog :meetup="meetup"></app-edit-product-details-dialog>
            </template>
          </v-card-title>
          <v-card-media
          v-bind:src="meetup.imageUrl" height="400px">
       </v-card-media>
       <v-card-text>
         <h5 class="info--text">
           {{ meetup.date | date }} - {{ meetup.location }}
         </h5>
         <div>
             <app-edit-product-date-dialog
            :meetup="meetup" v-if="userIsCreator"></app-edit-product-date-dialog>

           <app-edit-product-time-dialog
           :meetup="meetup" v-if="userIsCreator"></app-edit-product-time-dialog>

        </div>
         <div class="">
           {{ meetup.description }}
         </div>
       </v-card-text>
       <v-card-actions>
         <!-- <v-chip v-if="userIsAuthenticated && !userIsCreator"
          class ="green white--text">
          <v-avatar>
            <v-icon>check_circle</v-icon>
          </v-avatar>
            Registered
         </v-chip> -->
         <v-spacer></v-spacer>
         <app-register-dialog
          :meetupId="meetup.id"
          v-if="userIsAuthenticated && !userIsCreator"></app-register-dialog>
       </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    meetup () {
      return this.$store.getters.loadedMeetup(this.id)
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    },
    userIsCreator () {
      if (!this.userIsAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.meetup.createId
    },
    loading () {
      return this.$store.getters.loading
    }
  }
}
</script>

<style lang="css">
</style>



