<template lang="html">
  <v-container class="mt-4">
    <v-layout row wrap  v-for="meetup in meetups" :key="meetup.id" class="mb-2">
      <v-flex xs12 sm12 md12>
        <v-card class="info">
          <v-container fluid>
            <v-layout row>
              <v-flex xs6 sm6 md5>
                <v-card-media
                :src="meetup.imageUrl" height="200px">
                </v-card-media>
              </v-flex>
              <v-flex xs6 sm6 md7>
                <v-card-title primary-title>
                <div class="headline white--text">{{ meetup.title }}</div>
                <div class="">{{ meetup.date | date }}</div>
                <div class="mb-0">Listen to your favorite artists and albums whenenver and wherever, online and offline.</div>
              </v-card-title>
              <v-card-actions>
                <v-btn :to="'/meetups/' + meetup.id" flat class="orange--text">
                  <v-icon left light>arrow_forward</v-icon>
                  View Meetup
                </v-btn>
              </v-card-actions>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  computed: {
    meetups () {
      return this.$store.getters.loadedMeetups
    }
  }
}
</script>

<style lang="css">
</style>



